import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Pergunta_avaliacao_ideia } from '../../../modelo/Pergunta_avaliacao_ideia'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'pergunta_avaliacao_ideia-dados-details',   templateUrl: 'pergunta_avaliacao_ideia_dadosDetails.component.html' }) export class Pergunta_avaliacao_ideiaDadosDetailsComponent implements OnInit {    pergunta_avaliacao_ideia: Pergunta_avaliacao_ideia;    private sub: Subscription;    private errorMessage: string;    constructor(private route: ActivatedRoute, private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.pergunta_avaliacao_ideia.Pergunta_avaliacao_ideia";                 let id = +params['id'];         this.pergunta_avaliacao_ideia = new Pergunta_avaliacao_ideia();         this.pergunta_avaliacao_ideia.cd_pergunta_avaliacao_ideia = id;         o.objetoJson = this.pergunta_avaliacao_ideia;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a pergunta_avaliacao_ideia. '; }      this.pergunta_avaliacao_ideia = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   } }


